<template>
	<view class="container">
		<view class="border_bottom" style="	justify-content: space-between; align-items: center;padding: 0 10upx;  height: 70upx;">
			<view class="icontext" style="width: 60upx ; padding-left: 20upx;" @click="erweima">&#xe611;</view>
			<view class="" style="width: 550upx; background-color: #DDDDDD; height: 44upx;  border-radius: 11upx;padding-left: 10upx;">
				<text class="icontext" style="font-size: 30upx;padding: 5upx ;">&#xe60f;</text>
				<input type="text" class="" style="height: 20upx; font-size:24upx;position: relative;top: -5upx;" v-model="search_title"
				 placeholder=" 齿轮城市" />
			</view>
			<view class="" style="width: 100upx ;font-size:30upx ; padding-left: 20upx;" @click="search">搜索</view>
		</view>


		<view class="page-body">
			<scroll-view class="nav-left" scroll-y :style="'height:'+height+'px'">
				<view class="nav-left-item " :class="index==categoryActive?'nav-left-itemactive':''" @click="categoryClickMain(item1,index)"
				 :key="index" v-for="(item1,index) in categoryList">
					<view class="text_noactive" :class="index==categoryActive?'text_active':''" v-if="index==0">{{item1.NAME}}</view>
					<view class="text_noactive" :class="index==categoryActive?'text_active':''" v-if="index==1">{{item1.NAME}}</view>
					<view class="text_noactive" :class="index==categoryActive?'text_active':''" v-if="index==2">{{item1.NAME}}</view>
					<view class="text_noactive" :class="index==categoryActive?'text_active':''" v-if="index==3">{{item1.NAME}}</view>
					<view class="text_noactive" :class="index==categoryActive?'text_active':''" v-if="index==4">{{item1.NAME}}</view>
					<view class="text_noactive" :class="index==categoryActive?'text_active':''" v-if="index==5">{{item1.NAME}}</view>
					<view class="text_noactive" :class="index==categoryActive?'text_active':''" v-if="index==6">{{item1.NAME}}</view>
					<view class="icontext icon_view" :class="index==categoryActive?'icon_viewactive':''" v-if="index==0">&#xe624;</view>
					<view class="icontext icon_view" :class="index==categoryActive?'icon_viewactive':''" v-if="index==1">&#xe67b;</view>
					<view class="icontext icon_view" :class="index==categoryActive?'icon_viewactive':''" v-if="index==2">&#xe73c;</view>
					<view class="icontext icon_view" :class="index==categoryActive?'icon_viewactive':''" v-if="index==3">&#xe61b;</view>
					<view class="icontext icon_view" :class="index==categoryActive?'icon_viewactive':''" v-if="index==4">&#xe633;</view>
					<view class="icontext icon_view" :class="index==categoryActive?'icon_viewactive':''" v-if="index==5">&#xe645;</view>
					<view class="icontext icon_view" :class="index==categoryActive?'icon_viewactive':''" v-if="index==6">&#xe606;</view>
				</view>
			</scroll-view>
			<scroll-view class="nav-right" scroll-y :scroll-top="scrollTop" @scroll="scroll" :style="'height:'+height+'px'"
			 scroll-with-animation:true>
				<view :id="index==0?'first':''" class="nav-right-item " v-for="item in subCategoryList" :key="item">
					<image class="nav-left-item-pic" :src="pic_before_title_loc+item.LOGO" @click="tap_item(item.CARD_ID)" />
					<view class="hot-card-num-view">
						<text class="hot-card-num">{{item.TIME}}</text>
					</view>
					<view class="" style="font-size: 25upx; width: 95%; line-height: 60upx; padding:0 10upx ; justify-content: space-between;">
						<view>{{item.NAME}}</view>
						<view v-if="!item.COLLECTION" class="icontext" @click="tap_collection(item,index)"> &#xe696;</view>
						<view v-if="item.COLLECTION" style="color: red;" class="icontext" @click="tap_collection(item,index)">
							&#xe646;</view>
						<view style="icontext" @click="share(item)" class="icontext">&#xe619;</view>
					</view>


				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	// import luyouData from '../common/luyou.js'; 
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				search_title: "",
				luyouData: null,
				collect_list: [{}],
				// pic_before_title: "http://kaikaiomg-kaikaiomg.stor.sinaapp.com/uni_app_image/card_pic/",
				categoryList: [],
				subCategoryList: [],
				height: 0,
				categoryActive: 0,
				scrollTop: 0,
				scrollHeight: 0,
                pic_before_title_loc:'',


			}
		},
		computed: {
			...mapState(['hasLogin', 'userInfo', 'phonenum', 'openid'])

		},
		onLoad: async function() {
		    var that=this;
			 this.pic_before_title_loc=this.pic_before_title;
			this.height = uni.getSystemInfoSync().windowHeight - 70 * (uni.getSystemInfoSync().windowWidth / 750);
			// console.log(this.luyouData)
			
		    await this.get_hot_card_list()
			
			var post_data = {
				'form': 'get_collect_list',
				'openid': this.openid,
			};
			await this.download_from_server(post_data,'collect_list')
			
			uni.showLoading({
				title: '加载中。'
			})
			setTimeout(function() {
				uni.hideLoading();
				that.getCategory()
			}, 600);

		},
		methods: {
			scroll(e) {
				this.scrollHeight = e.detail.scrollHeight;
			},
			
			categoryClickMain(categroy, index) {
				this.categoryActive = index;
				this.subCategoryList = categroy.subCategoryList;
				this.scrollTop = -this.scrollHeight * index;
			},

			tap_collection(item, index) {
				// 				console.log(this.categoryList)
				// 				console.log(this.subCategoryList)

				if (this.categoryList[this.categoryActive].subCategoryList[index].COLLECTION) {
					this.upload_to_server({
						'form': 'card_collection',
						'card_id': this.subCategoryList[index].CARD_ID,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
						'collection_flag': 'false',
					});
					this.categoryList[this.categoryActive].subCategoryList[index].COLLECTION=false;
					uni.showToast({
						title: "取消关注",
						duration: 600,
					})
				} 
				else {
					this.upload_to_server({
						'form': 'card_collection',
						'card_id': this.subCategoryList[index].CARD_ID,
						'nickName': this.userInfo.nickName,
						'phonenum': this.phonenum,
						'openid': this.openid,
						'avatarUrl': this.userInfo.avatarUrl,
						'collection_flag': 'true',
					});
					this.categoryList[this.categoryActive].subCategoryList[index].COLLECTION=true;
					uni.showToast({
						title: "关注成功",
						duration: 600, 
					})

				}
		

				// this.subCategoryList[index]['COLLECTION']=true;
			},
		


			getCategory() {
				// await this.getData_card_list();
				var that = this;
				var collect_flag = false
				for (var i = 1; i < that.big_field_list.length; i++) {
					var subList = [];
					for (var j = 0; j < that.luyouData.length; j++) {
						
						if (that.luyouData[j].big_field == that.big_field_list[i]) {
					
							for (let k = 0; k < that.collect_list.length; k++) {
								if (that.luyouData[j].card_id == that.collect_list[k].card_id && that.collect_list[k].collection_flag == 'true') {
									collect_flag = true
									break;
								}
							    if (that.luyouData[j].card_id == that.collect_list[k].card_id && that.collect_list[k].collection_flag == 'false') {
									collect_flag = false
									break;
								}
							}
							subList.push({
								"NAME": that.luyouData[j].title,
								"LOGO": that.luyouData[j].pic_url,
								"CARD_ID": that.luyouData[j].card_id,
								"TIME": (that.luyouData[j].time).substring(0, 11),
								"COLLECTION": collect_flag,
							})

						}
					}
					that.categoryList.push({
						"NAME": that.big_field_list[i],
						"subCategoryList": subList
					})
				}
				//
				that.subCategoryList = that.categoryList[0].subCategoryList;

			},

			search: function() {
				uni.navigateTo({
					url: "../main_hot/hot?search_title=" + this.search_title,
				})
			},
			tap_item: function(card_id) {
				var url = "";
				var list_id = null
				for (var i = 0; i < this.luyouData.length; i++)
					if (this.luyouData[i].card_id == card_id) {
						url = this.luyouData[i].url;
						list_id = i;
					}
				uni.reLaunch({
					url: url + "?card_id=" + card_id + "&backpage=main_mulu&list_id=" + list_id,

				})

			}


		},

	}
</script>

<style>
	.container {
		display: flex;
		flex-direction: column;
	}



	

	.nav {
		display: flex;
		width: 100%;
	}

	.nav-left {
		width: 150upx;
	}

	.nav-left-item {
		height: 100upx;
		border-right: solid 1upx #E0E0E0;
		border-bottom: solid 1upx #E0E0E0;
		font-size: 25upx;
		display: flex;
		align-items: center;
		justify-content: center;
	}

	.nav-right {
		width: 600upx;
		justify-content: center;
	}

	.nav-right-item {
		flex-flow: column nowrap;
		width: 520upx;
		height: 260upx;
		box-sizing: border-box;
		text-align: center;
		position: relative;
		margin: 30upx 40upx 30upx 40upx;
		border-radius: 20upx;
		background-color: #FFFFFF;
	}

	.nav-left-item-pic {

		width: 100%;
		height: 200upx;
		border-top-left-radius: 20upx;
		border-top-right-radius: 20upx;


	}

	.hot-card-num {
		color: #FFFFFF;
		font-size: 26px;
		text-align: center;
	}

	.hot-card-num-view {
		background-color: #FF80AB;
		height: 30px;
		line-height: 30px;
		padding: 0 10px;
		justify-content: center;
		border-radius: 30px;
		position: absolute;
		top: 20px;
		right: 20px;
	}

	.nav-left-itemactive {
		background-color: WhiteSmoke;
	}

	.text_noactive {
		font-size: 28upx;
		color: #555555;

	}

	.text_active {
		font-size: 30upx;
		color: #0000FF;
	}

	.icon_view {
		color: #555555;
		font-size: 30upx;
		position: relative;
		left: 12upx;
		top: 0upx;

	}

	.icon_viewactive {
		color: #0000FF;
		font-weight: bold;
		font-size: 35upx;
		position: relative;
		left: 12upx;
		top: 0upx;
	}
</style>



